// CSS Grid layout
body {
  &.over-hidden {
    overflow: hidden;
  }
}

// wrap
#wrap {

  min-width:1400px;
  background-color:#f0f0f0;

  // main
  &.main {
    background-color:#1a1b1f;
    #header {
      background-color:transparent;
      .top-utill {
        li {
          &.alert {
            a {
              .num {                
                background-color:#000;
              }
            }
          }
        }
      }
    }
    #contents {
      padding-top:0;
    }
    #footer {
      .foot-logo {
        img {
          opacity: 0.3;
        }
      }
    }
  }
}

// contents
#contents {
  padding-top:131px;
}

// contents _ default
.contents-box {
  width:1400px;
  margin:0 auto;
  padding:70px 0;
  .location-area {
    display:flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom:30px;
    .location {
      color:#000;
      font-size:rem(13);
      font-weight:300;
      span {
        font-weight:400;
      }
    }
  }
  .contents-inner {
    padding:20px 22px 20px 22px;
    border:1px solid #e3e3e3;
    background-color:#fff;
  }
}