// detail _ 컨텐츠 상세
.detail-top {
  display:flex;
  justify-content: space-between;
  .movie-coment {
    flex:1;
    .coment {
      margin-top:15px;
      padding:20px 20px 20px 22px;
      color:#3e3e3e;
      font-size:rem(18);
      line-height:1.5;
      background-color:#f5f5f5;
    }
  }
  .con-summary {
    width:553px;
    padding:0 0 0 20px;
    .summary {
      padding:20px 0 0 30px;
      .photo {
        width:121px;
        height:121px;
        border-radius: 50%;
        overflow: hidden;
      }
      .tag {
        margin-top:35px;
        p {
          display:inline-block;
          padding:5px 7px 5px 7px;
          color:#fff;
          font-size:rem(12);
          line-height:1.2;
          background-color:#0d0e12;
          border-radius:2px;
        }
      }
      .title {
        margin-top:10px;
        color:#000;
        font-size:rem(32);
        font-weight:700;
        line-height:1.2;
      }
      .time {
        margin-top:20px;
        color:#000;
        font-size:rem(16);
        line-height:1.5;
        strong {
          font-weight:700;
        }
      }
      .quota {
        margin-top:20px;
        color:#000;
        font-size:rem(16);
        font-weight: 700;
        strong {
          color:#fe6600;
          font-weight: 700;
        }
      }
      .mem-like-reply {
        margin-top:28px;
        > p {
          display:inline-block;
          padding:0 0 0 28px;
          color:#000;
          font-size:rem(14);
          line-height:20px;
          background-repeat: no-repeat;
          background-position: 0 center;
          & + p {
            margin-left:35px;
          }
          &.mem {
            background-image:url("../images/icon/icon_member02.png");
          }
          &.like {
            background-image:url("../images/icon/icon_like02.png");
          }
          &.reply {
            background-image:url("../images/icon/icon_reply.png");
          }
        }
      }
    }
    .price {
      display:flex;
      flex-wrap:wrap;
      justify-content: space-between;
      align-items:center;
      margin-top:25px;
      padding:25px 0 0 30px;
      border-top:1px solid #dfdfdf;
      .num {
        color:#000;
        font-size:rem(16);
        font-weight:500;
        line-height:1.2;
        strong {
          font-size:rem(28);
          font-weight:700;
        }
      }
      .join {
        width:100%;
        margin-top:25px;
      }
    }
  }
}

.detail-content {
  margin-top:60px;
  .con-inner {
    padding:65px 70px 50px;
    > div {
      display:none;
    }
  }
}

// search-box-1
.search-box-1 {
  position:relative;
  width:384px;
  height:43px;
  border:1px solid #b0b0b0;
  border-radius:18px;
  input[type=text] {
    width:100%;
    height:41px;
    padding:0 50px 0 28px;
    color:#818181;
    font-size:rem(16);
    border:none;
    background:none;
    outline: none;
  }
  button {
    position:absolute;
    top:0;
    right:0;
    bottom:0;
    width:50px;
    padding:0;
    border:none;
    background:none;
    cursor: pointer;
    outline: none;
  }
}

// box-type
.box-type-1 {
  padding:40px;
  border-bottom:1px solid #000;
}