// title box
.title-box-1 {
  display:flex;
  justify-content: space-between;
  align-items: flex-end;
  padding-bottom:30px;
  border-bottom:1px solid #000;
}

// title
.title-type-1 {
  margin-bottom:15px;
  color:#1f1d1b;
  font-size:rem(24);
  font-weight:700;
  line-height:1.2;
}

// text
.text-type-1 {
  color:#a5a5a5;
  font-size:rem(16);
  font-weight:500;
  line-height:1.5;
  strong {
    color:#ff6600;
  }
  .txt-1 {
    color:#000;
  }
}
.text-type-2 {
  color:#000;
  font-size:rem(19);
  line-height:1.5;
}
.text-type-3 {
  color:#666;
  font-size:rem(16);
  line-height:1.5;
}