// input type
.form-input {
  position:relative;
  margin-bottom:15px;
  input[type=text],
  input[type=password],
  input[type=email],
  input[type=tel],
  textarea {
    width:100%;
    height:57px;
    padding:0 10px 0 33px;
    color:#333;
    font-size:rem(15);
    font-weight:500;
    border:1px solid #cacaca;
  }
  textarea {
    padding-top:10px;
    padding-bottom:10px;
    font-family: $font-main;
  }

  > button {
    position:absolute;
    top:0;
    right:0;
    bottom:0;
  }

  &.btn {
    input {
      padding-right:120px;
    }
  }

  &.btn-2 {
    display:flex;
    input {
      width:64%;
    }
    button {
      width:34%;
    }
  }

  // size
  &.size-sm {
    input[type=text],
    input[type=password],
    input[type=email],
    input[type=tel],
    textarea {
      height:48px;
      padding:0 10px 0 22px;
      color:#9a9a9a;
      font-size:rem(16);
      font-weight:400;
    }
    textarea {
      height:150px;
      padding-top:10px;
      padding-bottom:10px;
      padding-left:22px;
    }
  }

  // color
  &.bg-color-1 {
    input[type=text],
    input[type=password],
    input[type=email],
    input[type=tel],
    textarea {
      background-color:#f1f1f1;
    }
  }

  ::placeholder {
    color:#aaa;
  }
}

// checkbox
.checkbox {
  display:inline-block;
  position:relative;
  input[type=checkbox] {
    position:absolute;
    top:0;
    left:0;
    opacity:0;
  }
  label {
    display:inline-block;
    height:22px;
    line-height:22px;
    padding:0 0 0 36px;
    color:#000;
    font-size:rem(13);
    background:url("../images/common/form_check_off.png") no-repeat 0 center;
    .txt-required {
      color:#ff5000;
    }
  }
  input[type=checkbox]:checked + label {
    background-image:url("../images/common/form_check_on.png");
  }
}

// coment
.form-text-coment-1 {
  margin:-5px 0 0 7px;
  color:#919191;
  font-size:rem(14);
}
.form-text-coment-2 {
  color:#979797;
  font-size:rem(14);
  line-height:1.2;
  letter-spacing: -0.05rem;
}